.header {
    height: 95vh;
    background-image: -webkit-gradient(
        linear,
        left top, right bottom,
        from(rgba(51, 51, 51,0.8)),
        to(rgba(221, 24, 24,0.8))), 
        url(https://scontent-sit4-1.xx.fbcdn.net/v/t31.0-8/17358720_1710696889227821_8364957511710094062_o.jpg?oh=b194a5e08715f1261f9dd7c24bd6fd1b&oe=5ADBC137);
    background-image: linear-gradient(
        to right bottom,
        rgba(51, 51, 51,0.8),
        rgba(221, 24, 24,0.8)), 
        url(https://scontent-sit4-1.xx.fbcdn.net/v/t31.0-8/17358720_1710696889227821_8364957511710094062_o.jpg?oh=b194a5e08715f1261f9dd7c24bd6fd1b&oe=5ADBC137);
    background-size: cover;
    background-position: top;
    position: relative;
    /* to crop the graph */
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 75vh, 0 100%);
            clip-path: polygon(0 0, 100% 0, 100% 75vh, 0 100%)
}

.header__logo-box {
    position: absolute;
    top: 4rem;
    left: 4rem; 
}

.header__locale-group{
    position: absolute;
    top: 2rem;
    right: 2rem; 
}

.header__logo {
    height: 6.5rem;
}

.header__text-box {
    position: absolute;
    top: 10%;
    left: 50%;
    /* to centre the element */
    -webkit-transform: translate(-50%, 50%);
            transform: translate(-50%, 50%);
    text-align: center;
}

.heading-primary {
    color: #ffffff;
    text-transform: uppercase;

    -webkit-backface-visibility: hidden;

            backface-visibility: hidden;
    margin-bottom: 6rem;
}

.heading-primary--main {
    display: block;
    font-size: 6rem;
    font-weight: 400;
    letter-spacing: 0.2rem;
    -webkit-animation-name: moveInLeft;
            animation-name: moveInLeft;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    /* animation-iteration-count: 3; */
    /* animation-delay: 3s; */
}

.heading-primary--sub {
    display: block;
    font-size: 2rem;
    font-weight: 700;
    letter-spacing: 1.74rem;
    /* can write all together */
    -webkit-animation: moveInRight 1s ease-out;
            animation: moveInRight 1s ease-out;
    /* animation-name: moveInRight;
    animation-duration: 1s; */
}

@-webkit-keyframes moveInLeft { 
    0% {
        opacity: 0;
        -webkit-transform: translateX(-10rem);
                transform: translateX(-10rem);
        /* ctrl+D select multiple same word */
    }

    80% {
        -webkit-transform: translateX(1rem);
                transform: translateX(1rem);
    }

    100% {
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}

@keyframes moveInLeft { 
    0% {
        opacity: 0;
        -webkit-transform: translateX(-10rem);
                transform: translateX(-10rem);
        /* ctrl+D select multiple same word */
    }

    80% {
        -webkit-transform: translateX(1rem);
                transform: translateX(1rem);
    }

    100% {
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}

@-webkit-keyframes moveInRight { 
    0% {
        opacity: 0;
        -webkit-transform: translateX(10rem);
                transform: translateX(10rem);
    }

    80% {
        -webkit-transform: translateX(-1rem);
                transform: translateX(-1rem);
    }

    100% {
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}

@keyframes moveInRight { 
    0% {
        opacity: 0;
        -webkit-transform: translateX(10rem);
                transform: translateX(10rem);
    }

    80% {
        -webkit-transform: translateX(-1rem);
                transform: translateX(-1rem);
    }

    100% {
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}

@-webkit-keyframes moveInBottom { 
    0% {
        opacity: 0;
        -webkit-transform: translateY(3rem);
                transform: translateY(3rem);
    }

    100% {
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}

@keyframes moveInBottom { 
    0% {
        opacity: 0;
        -webkit-transform: translateY(3rem);
                transform: translateY(3rem);
    }

    100% {
        opacity: 1;
        -webkit-transform: translate(0);
                transform: translate(0);
    }
}

.btn:link,
.btn:visited {
    text-transform: uppercase;
    text-decoration: none;
    padding: 1.5rem 4rem;
    display: inline-block;
    border-radius: 10rem;
    -webkit-transition: all .2s;
    transition: all .2s;
    position: relative;
    font-size: 1.6rem;
}

.btn:hover {
    -webkit-transform: translateY(-.3rem);
            transform: translateY(-.3rem);
    -webkit-box-shadow: 0 1rem 2rem rgba(0,0,0,0.2);
            box-shadow: 0 1rem 2rem rgba(0,0,0,0.2);
}

.btn:active {
    -webkit-transform: translateY(-.1rem);
            transform: translateY(-.1rem);
    -webkit-box-shadow: 0 .5rem 1rem rgba(0,0,0,0.2);
            box-shadow: 0 .5rem 1rem rgba(0,0,0,0.2);
}

.btn--white {
    background-color: #fff;
    color: #777;
}

/* psudeo element is a child of the actual element ::after*/

.btn::after {
    content: "";
    display: inline-block;
    height: 100%;
    width: 100%;
    border-radius: 10rem;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    -webkit-transition: all .4s;
    transition: all .4s
}

.btn--white::after {
    background-color: #ffffff;
}

.btn:hover::after {
    -webkit-transform: scaleX(1.4) scaleY(1.6);
            transform: scaleX(1.4) scaleY(1.6);
    opacity: 0;
}

.btn--animated {
    -webkit-animation: moveInBottom .5s ease-out;
            animation: moveInBottom .5s ease-out;
    -webkit-animation-delay: 0.75s;
            animation-delay: 0.75s;
    -webkit-animation-fill-mode: backwards;
            animation-fill-mode: backwards;/*auto apply 0% state before animation starts*/
}

.btn:link.btn--locale {
    padding: 1.5rem 2.5rem;
    display: inline-block;
    border-radius: 5rem;
    -webkit-transition: all .2s;
    transition: all .2s;
    position: relative;
    font-size: 1rem;
}































































